import React, { useState, useEffect } from "react";
import { motion, AnimatePresence, useScroll, useMotionValueEvent } from "framer-motion";
import { cn } from "@/lib/utils";
export const FloatingNav = ({
  navItems,
  className
}: {
  navItems: {
    name: string;
    link: string;
    icon?: JSX.Element; // This can still accept custom icons if needed
  }[];
  className?: string;
}) => {
  const {
    scrollYProgress
  } = useScroll();
  const [visible, setVisible] = useState(true);
  const [activeSection, setActiveSection] = useState(navItems[0].link); // Set the initial active section
  const [underlineStyle, setUnderlineStyle] = useState({
    width: "0px",
    transform: "translateX(0)"
  }); // Style for the underline

  useMotionValueEvent(scrollYProgress, "change", current => {
    if (typeof current === "number") {
      let direction = current - scrollYProgress.getPrevious()!;
      if (scrollYProgress.get() < 0.05) {
        setVisible(true);
      } else {
        setVisible(direction < 0);
      }
    }
  });
  const handleScroll = (e: React.MouseEvent, link: string) => {
    e.preventDefault();
    const section = document.querySelector(link);
    if (section) {
      const offset = 100; // Adjust based on navbar height
      const topPosition = section.getBoundingClientRect().top + window.scrollY - offset;
      window.scrollTo({
        top: topPosition,
        behavior: "smooth"
      });
      setActiveSection(link); // Update active section on click
    }
  };
  useEffect(() => {
    const handleScrollEvent = () => {
      navItems.forEach(navItem => {
        const section = document.querySelector(navItem.link);
        if (section) {
          const {
            top,
            bottom
          } = section.getBoundingClientRect();
          // Check if the section is in the viewport
          if (top <= window.innerHeight / 2 && bottom >= window.innerHeight / 2) {
            setActiveSection(navItem.link);
          }
        }
      });
    };
    window.addEventListener("scroll", handleScrollEvent);
    return () => window.removeEventListener("scroll", handleScrollEvent);
  }, [navItems]);
  useEffect(() => {
    const activeLink = document.querySelector(`a[href='${activeSection}']`) as HTMLElement;
    if (activeLink) {
      const {
        offsetWidth,
        offsetLeft
      } = activeLink;
      setUnderlineStyle({
        width: `${offsetWidth}px`,
        transform: `translateX(${offsetLeft - 10}px)` // Shift underline left by 10px
      });
    }
  }, [activeSection]);
  return <AnimatePresence mode="wait" data-sentry-element="AnimatePresence" data-sentry-component="FloatingNav" data-sentry-source-file="FloatingNavbar.tsx">
      <motion.div initial={{
      opacity: 1,
      y: -100
    }} animate={{
      y: visible ? 0 : -100,
      opacity: visible ? 1 : 0
    }} transition={{
      duration: 0.2
    }} className={cn("relative flex max-w-fit md:min-w-[70vw] lg:min-w-fit fixed z-[5000] top-10 inset-x-0 mx-auto px-10 py-5 rounded-lg border border-black/.1 shadow-[0px_2px_3px_-1px_rgba(0,0,0,0.1),0px_1px_0px_0px_rgba(25,28,33,0.02),0px_0px_0px_1px_rgba(25,28,33,0.08)] items-center justify-center space-x-4", className)} style={{
      backdropFilter: "blur(16px) saturate(180%)",
      backgroundColor: "rgba(17, 25, 40, 0.75)",
      borderRadius: "12px",
      border: "1px solid rgba(255, 255, 255, 0.125)"
    }} data-sentry-element="unknown" data-sentry-source-file="FloatingNavbar.tsx">
        {/* Add Home Button with Inline SVG */}
        <a href="#home" onClick={e => {
        e.preventDefault();
        window.scrollTo({
          top: 0,
          behavior: "smooth"
        }); // Scroll to top smoothly
      }} className={cn("relative dark:text-neutral-50 items-center flex space-x-1 text-neutral-600 dark:hover:text-neutral-300 hover:text-neutral-500 cursor-pointer")}>
          {/* Inline SVG for Home Icon */}
          <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor" data-sentry-element="svg" data-sentry-source-file="FloatingNavbar.tsx">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 9l9-7 9 7v11a2 2 0 01-2 2H5a2 2 0 01-2-2V9z" data-sentry-element="path" data-sentry-source-file="FloatingNavbar.tsx" />
          </svg>
          <span className="text-sm sm:text-base">Home</span>
        </a>

        {navItems.map((navItem, idx) => <a key={`link=${idx}`} href={navItem.link} onClick={e => handleScroll(e, navItem.link)} className={cn("relative dark:text-neutral-50 items-center flex space-x-1 text-neutral-600 dark:hover:text-neutral-300 hover:text-neutral-500 cursor-pointer")}>
            <span className="block sm:hidden">{navItem.icon}</span>
            <span className="text-sm sm:text-base">{navItem.name}</span>
          </a>)}

        {/* Underline Animation */}
        <motion.div className="absolute h-[5px] bg-blue-500" style={{
        ...underlineStyle,
        transition: "width 0.3s ease, transform 0.3s ease",
        bottom: "-5px",
        // Adjust this to position it correctly below the links
        left: 0
      }} data-sentry-element="unknown" data-sentry-source-file="FloatingNavbar.tsx" />
      </motion.div>

      <style jsx>{`
        @media (max-width: 768px) {
          .floating-nav {
            width: 90%; // Adjust width for mobile
            padding: 15px; // Adjust padding for mobile
          }
          .floating-nav a {
            font-size: 0.875rem; // Adjust text size for mobile
          }
          .floating-nav .underline {
            bottom: -7px; // Adjust underline position for mobile
          }
        }
      `}</style>
    </AnimatePresence>;
};