"use client";

import { useEffect } from "react";
import { motion, stagger, useAnimate } from "framer-motion";
import { cn } from "@/lib/utils";
export const TextGenerateEffect = ({
  words,
  className
}: {
  words: string;
  className?: string;
}) => {
  const [scope, animate] = useAnimate();
  let wordsArray = words.split(" ");
  useEffect(() => {
    console.log(wordsArray);
    animate("span", {
      opacity: 1
    }, {
      duration: 2,
      delay: stagger(0.2)
    });
  }, [scope.current]);
  const renderWords = () => {
    return <motion.div ref={scope} data-sentry-element="unknown" data-sentry-component="renderWords" data-sentry-source-file="TextGenerateEffect.tsx">
        {wordsArray.map((word, idx) => {
        return <motion.span key={word + idx}
        // change here if idx is greater than 3, change the text color to #CBACF9
        className={` ${idx > 3 ? "text-purple" : "dark:text-white text-black"} opacity-0`}>
              {word}{" "}
            </motion.span>;
      })}
      </motion.div>;
  };
  return <div className={cn("font-bold", className)} data-sentry-component="TextGenerateEffect" data-sentry-source-file="TextGenerateEffect.tsx">
      {/* mt-4 to my-4 */}
      <div className="my-4">
        {/* remove  text-2xl from the original */}
        <div className=" dark:text-white text-black leading-snug tracking-wide">
          {renderWords()}
        </div>
      </div>
    </div>;
};